import { Form as LegacyForm } from '@ant-design/compatible'
import { Button, Card, Input, message, Modal, Radio, Tooltip, Switch, TreeSelect } from 'antd'
import { connect } from 'dva'
import _ from 'lodash'
import React, { useState} from 'react'

import SelectFieldsModal from './SelectFieldsModal'

const formLayout = {
    labelCol: {
        xs: {span: 24},
        sm: {span: 6},
    },
    wrapperCol: {
        xs: {span: 24},
        sm: {span: 18},
    },
}

const OptionsModal = LegacyForm.create()(props => {
    const {selectedRows, menus} = props

    const [mode, setMode] = useState('auto')
    const [visible, setVisible] = useState(false)
    const [fieldsModalVisible, setFieldsModalVisible] = useState(false)

    const handleSetVisible = () => {
        if (!selectedRows || selectedRows.length === 0) {
            return message.info('')
        }

        if (selectedRows.length > 1) {
            return message.info('')
        }

        setVisible(true)
    }

    const showListTip = () => message.info('')
    const handleSetInvisible = () => setVisible(false)
    const handleShowSelectFields = () => setFieldsModalVisible(true)
    const handleHideSelectFields = () => setFieldsModalVisible(false)
    const onChangeMode = e => setMode(e.target.value)
    const onParentMenuChange = () => {}

    const renderFooter = () => {
        return (
            <div>
                <Button onClick={() => generateCode}>
                    
                </Button>
                { mode === 'auto' ? (
                    <Button type="primary" onClick={() => generateCode}>
                        
                    </Button>
                ) : (
                    <Button type="primary" onClick={() => generateCode}>
                        
                    </Button>
                )}
            </div>
        )
    }

    const {getFieldDecorator} = props.form
    const modelMeta = selectedRows && selectedRows.length > 0 ? selectedRows[0] : null
    let categoryName = _.get(modelMeta, ['category_name'])
    categoryName = (!categoryName || categoryName === 'default') ? '' : `${categoryName}/`

    return (
        <>
            <Button type='primary' onClick={handleSetVisible}>
                
            </Button>
            {visible && modelMeta ? (
                <Modal
                    centered
                    visible
                    destroyOnClose
                    width={800}
                    bodyStyle={{ height: 500, overflow: 'scroll', backgroundColor: '#fff' }}
                    title=""
                    footer={renderFooter()}
                    onCancel={handleSetInvisible}
                >
                    <LegacyForm {...formLayout} labelAlign="left" layout="horizontal">
                        <Card title="" bordered={false}>
                            <LegacyForm.Item label=''>
                                {getFieldDecorator('mode')(
                                    <Radio.Group onChange={onChangeMode}>
                                        <Radio value="auto" style={{width: 200}}></Radio>
                                        <Radio value="manual"></Radio>
                                    </Radio.Group>
                                )}
                                {mode === 'auto' ? (
                                    <div>
                                        <li>: , </li>
                                        <li>: </li>
                                    </div>
                                ) : (
                                    <div>
                                        <li>: </li>
                                        <li>: </li>
                                    </div>
                                )}
                            </LegacyForm.Item>
                            <LegacyForm.Item label=''>
                                {mode === 'auto' ? (
                                    <>
                                        <span></span>
                                        {getFieldDecorator('_menu')(
                                            <Input />
                                        )}
                                        <span></span>
                                        {getFieldDecorator('parent_menu')(
                                            <TreeSelect
                                                value=""
                                                treeData={menus}
                                                placeholder=""
                                                treeDefaultExpandAll
                                                onChange={onParentMenuChange}
                                            />
                                        )}
                                        <span></span>
                                        {getFieldDecorator('parent_menu')(
                                            <Input type="number" />
                                        )}
                                    </>
                                ) : (
                                    <div style={{lineHeight: '22px', marginTop: '10px'}}>
                                        <p>
                                            /src/pages /config/config.js<br />
                                            
                                        </p>
                                        <pre>
                                            {JSON.stringify({
                                                name: modelMeta.name.toLowerCase(),
                                                icon: ' Ant Design ',
                                                path: `${categoryName}${modelMeta.name.toLowerCase()}`,
                                                component: `./${categoryName}${modelMeta.name.toLowerCase()}`,
                                            }, null, 4)}
                                        </pre>
                                    </div>
                                )}
                            </LegacyForm.Item>
                        </Card>
                        <Card title="" bordered={false}>
                            <LegacyForm.Item label='' extra="">
                                {getFieldDecorator('list')(
                                    <Switch checked onChange={showListTip} />
                                )}
                                <Button style={{marginLeft: '10px', width: '125px'}} onClick={handleShowSelectFields}></Button>
                                <SelectFieldsModal modelMeta={modelMeta} visible={fieldsModalVisible} />
                            </LegacyForm.Item>
                            <LegacyForm.Item label=''>
                                {getFieldDecorator('create')(
                                    <Switch defaultChecked />
                                )}
                                <Tooltip title=", ...">
                                    <Button disabled style={{marginLeft: '10px', width: '125px'}}></Button>
                                </Tooltip>
                            </LegacyForm.Item>
                            <LegacyForm.Item label=''>
                                {getFieldDecorator('edit')(
                                    <Switch defaultChecked />
                                )}
                                <Tooltip title=", ...">
                                    <Button disabled style={{marginLeft: '10px', width: '125px'}}></Button>
                                </Tooltip>
                            </LegacyForm.Item>
                            <LegacyForm.Item label=''>
                                {getFieldDecorator('delete')(
                                    <Switch defaultChecked />
                                )}
                            </LegacyForm.Item>
                            <LegacyForm.Item label='/'>
                                {getFieldDecorator('import-export')(
                                    <Switch defaultChecked />
                                )}
                            </LegacyForm.Item>
                            <LegacyForm.Item label=''>
                                {getFieldDecorator('search')(
                                    <Switch defaultChecked />
                                )}
                            </LegacyForm.Item>
                            <LegacyForm.Item label=' JSON'>
                                {getFieldDecorator('viewJson')(
                                    <Switch defaultChecked />
                                )}
                            </LegacyForm.Item>
                        </Card>
                    </LegacyForm>
                </Modal>
            ) : null}
        </>
    )
})

export default connect(({ global }) => ({
    menus: global.menus
}))(OptionsModal)
