import { connect } from 'dva'

import ModelManageLayout from '../layouts/ModelManageLayout'

/**
 *  Model  crud 
 */
class CommonCrud extends ModelManageLayout {
    constructor(props) {
        super()
        this.currentUser = props.currentUser
    }
}

export default connect(({ user }) => ({
    currentUser: user.currentUser
}))(CommonCrud)
